<template>
  <div class="home">
    <div
      class="pre-loader"
      v-if="isVisible"
    >
      <Preloader/>
    </div>
    <div class="header-wrapper">
      <div>По умолчанию выводятся заказы за текущий месяц.</div>
      <form class="wrapper-datepicker" @submit.prevent="getDataByDate($event)">
        <label for="data1">Показать заказы с</label>
        <input id="data1" type="text" class="datepicker" required>
        <label for="data2"> по</label>
        <input id="data2" type="text" class="datepicker" required>
        <button
          class="btn-small waves-effect waves-light"
          type="submit"
        >
          Загрузить
        </button>
      </form>
    </div>
    <div class="wrapper-note-date">
      <span style="margin-right: 10px;" class="right">

        <span style="padding-right: 5px">Заказы показанны с </span>
        <span class="note-date">{{fromDate}}</span>
        <span style="padding: 0 5px">по</span>
        <span class="note-date">{{ toDate }}</span>
        <i style="padding-left: 5px" class="material-icons">info_outline</i>
      </span>
    </div>
    <span class="not-dispatched" style="margin-left: 10px">
      Не отправленных посылок:
      <span v-if="!preloader" style="margin-left: 5px" class="not-dispatched-count">
        {{ this.notDispatched.length || 0}}
      </span>
      <template v-if="preloader">
        <preloaderT />
      </template>
    </span>
    <tabs @clicked="tabClicked">
      <tab v-for="(i, index) in SHOPS"
            :key="index"
            :name="i"
      >
        <table>
          <thead>
          <tr>
            <th>Имя покупателя</th>
            <th>Наименование</th>
            <th
              class="date-sold"
              @click="sortByDate"
            >
              Дата продажи
              <i class="material-icons">swap_vert</i>
            </th>
            <th>Номер отслеживания</th>
            <th>Статус отправления</th>
          </tr>
          </thead>

          <tbody @click="showMoreTitle($event)">
          <tr style="position: relative" v-for="(order, index) of DATA.orders" :key="index">
            <td>{{order.buyerName}}</td>
            <td class="more_info_title">
                {{normalizeTitle(order.title, order)}}
                <i
                  :data-index="index"
                  title="Смотреть полное название товара."
                  class="material-icons"
                >
                  read_more
                </i>
            </td>
            <td>{{normalizeOrderDate(order.date.slice(0, 10))}}</td>
            <td
              style="display: flex;align-items: center"
              :class="{
              'brown-text darken-4': order.trackNumbers === null,
               'text-blue': !!order.trackNumbers}"
            >
              {{order.trackNumbers === null ? helperText : order.trackNumbers[0].number}}
              <i v-if="order.trackNumbers === null" style="padding-left: 5px" class="material-icons">info_outline</i>
            </td>
            <td
              class="track-number-td"
              :class="{
                'red-text text-darken-1': typeof order['statusTrack'] !== 'undefined',
                'green-text accent-2': typeof order['statusTrack'] === 'undefined'
              }"
            >
              <preloaderT v-if="preloader"/>
              <template v-if="!preloader">
                {{typeof order['statusTrack'] !== 'undefined' ? notTracked : isOkText}}
                <i :data-index="index"
                   title="Не отслеживается!."
                   class="material-icons done-icon"
                   :class="{'red-text text-darken-3': typeof order['statusTrack'] !== 'undefined'}"
                   v-if="typeof order['statusTrack'] !== 'undefined'"
                >
                  report_problem
                </i>
                <i :data-index="index"
                   title="Посылка принята Укрпочтой."
                   class="material-icons done-icon"
                   :class="{'red-text text-darken-3': typeof order['statusTrack'] !== 'undefined'}"
                   v-if="typeof order['statusTrack'] === 'undefined'"
                >
                  done
                </i>
              </template>
            </td>
          </tr>
          </tbody>
        </table>
      </tab>
    </tabs>
    <div
      class="helper-text-for-data"
      v-if="DATA === ''"
    >
      Данные не загружены.
    </div>
    <div
      class="helper-text-for-data"
      v-if="isData"
    >
      С {{ fromDate }}  по  {{ toDate }}  заказы отсутствуют.
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import localize from "@/utils/localize";
import Preloader from '@/components/preloader'
import preloaderT from "@/components/preloaderT";
import filter from "@/utils/filter";
import {dateFormat} from '@vuejs-community/vue-filter-date-format'
import {mapGetters, mapActions} from 'vuex'

export default {
  name: 'Home',
  components: {
    Preloader,
    preloaderT
  },
  data: () => ({
    preloader: false,
    notDispatched: [],
    fromDate: '',
    toDate: '',
    currentShop: '',
    defaultShop: '',
    isDataOrders: false,
    regexp: /&#11088;/gi,
    isShowTitle: false,
    helperText: 'Трек номер отсутствует в системе Seller-Online.',
    isVisible: true,
    notTracked: 'Трек номер отсутствует в системе Укрпочты.',
    isTracked: '',
    isOkText: 'Посылка принята в обработку',
    orderBy: true,
    ordersByDateInsert: 'insert_date_asc',
    insertDateDesc: 'insert_date_desc'
  }),
  computed: {
    ...mapGetters(['SHOPS', 'DATA']),
    isData: function () {
      if (this.DATA !== '') {
        if(this.DATA.orders.length === 0) return true
      }
    },
    normalizeFromDate: function() {
      return dateFormat(new Date()).split(' ')[0].split('-').slice(0, 2).join('-') + '-01'
    },
    normalizeDateTo: function() {
      return dateFormat(new Date()).split(' ')[0]
    }
  },
  methods: {
    ...mapActions(['main', 'GET_DATA', 'TRACE']),
    sortByDate() {
      this.notDispatched = []
      if(this.orderBy) {
        localStorage.setItem(
            this.currentShop,
            JSON.stringify({
              fromDate: this.fromDate,
              toDate: this.toDate,
              orderBy: this.ordersByDateInsert
            })
        )
        this.GET_DATA(filter(this.fromDate, this.toDate, this.currentShop || this.defaultShop, this.ordersByDateInsert))
            .then(() => {
              this.trace()
              this.isVisible = false
              this.orderBy = false
            })
            .catch(e => console.log(e))
      } else {
        localStorage.setItem(
            this.currentShop,
            JSON.stringify({
              fromDate: this.fromDate,
              toDate: this.toDate,
              orderBy: this.insertDateDesc
            })
        )
        this.GET_DATA(filter(this.fromDate, this.toDate, this.currentShop || this.defaultShop, this.insertDateDesc))
            .then(() => {
              this.trace()
              this.isVisible = false
              this.orderBy = true
            })
            .catch(e => console.log(e))
      }
    },
    normalizeOrderDate: function (date) {
      return date.split('-').reverse().join('-')
    },
    async trace() {
      this.preloader = true
      for (const [index, order] of this.DATA.orders.entries()) {
        const obj = {
          name: order.trackNumbers !== null ? order.trackNumbers[0].number : ''
        }
        try {
          await this.TRACE(obj).then(response => {
            const key = Object.keys(response.data.BarcodeInfoService.lastoffice).length
            if(key === 0) {
              order.statusTrack = false
              this.notDispatched.push(order)
              this.$store.state.data.orders.splice(index, 1, order)
            }
          }).catch(e => console.log(e))
        } catch (e) {
          console.log(e)
        }
      }
      this.preloader = false
    },
    showMoreTitle(event) {
      if(event.target.className !== 'more_info_title' && event.target.tagName !== 'I') return
      const index = event.target.childNodes[1] ? event.target.childNodes[1].dataset.index : event.target.dataset.index
      const item = this.DATA.orders[index]
      if(item.showFullText === 'true') {
        item.showFullText = 'false'
        this.$store.state.data.orders.splice(index, 1, item)
        if(event.target.className === 'more_info_title') {
          event.target.style.position = ''
          event.target.style.backgroundColor = ''
        } else {
          event.target.parentNode.style.position = ''
          event.target.parentNode.style.backgroundColor = ''
        }
      } else {
        item.showFullText = 'true'
        this.$store.state.data.orders.splice(index, 1, item)
        if(event.target.className === 'more_info_title') {
          event.target.style.position = 'absolute'
          event.target.style.backgroundColor = 'white'
        } else {
          event.target.parentNode.style.position = 'absolute'
          event.target.parentNode.style.backgroundColor = 'white'
        }
      }
    },
    normalizeTitle (str, order) {
      if(order.showFullText === 'true') {
        return str.replace(this.regexp, '')
      } else {
        return str.replace(this.regexp, '').slice(0, 26) + '...'
      }
    },

    async helperGetFunctionShopName() {
      const date = JSON.parse(localStorage.getItem(this.currentShop))
      if(date !== null) {
        await this.GET_DATA(filter(date.fromDate, date.toDate, this.currentShop || this.defaultShop, date.orderBy))
      } else {
        await this.GET_DATA(filter(this.fromDate, this.toDate, this.currentShop || this.defaultShop, this.insertDateDesc))
      }
    },
    getDataByDate (event) {
      this.notDispatched = []
      this.isVisible = true
      const _fromDate = event.target.querySelector('input#data1')
      const _toDate = event.target.querySelector('input#data2')
      this.fromDate = _fromDate.value.split(' ').join('-')
      this.toDate = _toDate.value.split(' ').join('-')
      const date = JSON.parse(localStorage.getItem(this.currentShop))
      console.log(date)
      if(date !== null) {
        localStorage.setItem(
            this.currentShop,
            JSON.stringify({
              fromDate: this.fromDate,
              toDate: this.toDate,
              orderBy: date.orderBy
            })
        )
        this.GET_DATA(filter(this.fromDate, this.toDate, this.currentShop || this.defaultShop, date.orderBy))
            .then(() => {
              this.trace()
              _fromDate.value = ''
              _toDate.value = ''
              this.isVisible = false
            })
            .catch(e => console.log(e))
      } else {
        console.log(this.currentShop)
        localStorage.setItem(
          this.currentShop || this.defaultShop,
          JSON.stringify({
            fromDate: this.fromDate,
            toDate: this.toDate,
            orderBy: this.insertDateDesc
          })
        )
        this.GET_DATA(filter(this.fromDate, this.toDate, this.currentShop || this.defaultShop, this.insertDateDesc))
            .then(() => {
              this.trace()
              _fromDate.value = ''
              _toDate.value = ''
              this.isVisible = false
            })
            .catch(e => console.log(e))
      }

    },
    tabClicked (selectedTab) {
      this.notDispatched = []
      this.isVisible = true
      this.currentShop = selectedTab.tab.name
      const date = JSON.parse(localStorage.getItem(selectedTab.tab.name))
      if(date !== null) {
        this.fromDate = date.fromDate
        this.toDate = date.toDate
        this.GET_DATA(filter(date.fromDate, date.toDate, selectedTab.tab.name || this.defaultShop, date.orderBy))
            .then(() => {
              this.trace()
            })
            .catch(e => console.log(e))
            .finally(() => this.isVisible = false)
      } else {
        this.GET_DATA(filter(this.fromDate, this.toDate, selectedTab.tab.name || this.defaultShop, this.insertDateDesc))
            .then(() => {
              this.trace()
            })
            .catch(e => console.log(e))
            .finally(() => this.isVisible = false)
      }
    }
  },
  mounted() {
    this.isVisible = true
    this.main()
        .then(() => {
          const date = JSON.parse(localStorage.getItem(this.SHOPS[0]))
          if(date !== null) {
            this.currentShop = this.SHOPS[0]
            this.fromDate = date.fromDate
            this.toDate = date.toDate
          } else {
            this.fromDate = this.normalizeFromDate
            this.toDate = this.normalizeDateTo
            this.defaultShop = this.SHOPS[0]
          }
        })
        .then(() => this.helperGetFunctionShopName())
        .then(() => {
          this.trace()
        })
        .then(() => {
          //this.$el.querySelectorAll('.tabs-component-panel')[0].style.display = 'block'
          this.isVisible = false
        })
        .catch(e => console.log(e))
    const dataPickers = document.querySelectorAll('.datepicker');
    M.Datepicker.init(dataPickers, {
      showClearBtn: true,
      format: 'yyyy mm dd',
      i18n: localize
    })
  }
}
</script>
